import {
    CardActionArea, Container, Fab, makeStyles, Modal,
    TextField, MenuItem, Radio, RadioGroup, FormControlLabel,
    FormLabel, Button,

}
from "@material-ui/core"
import AddIcon from '@mui/icons-material/Add';
import Tooltip from '@mui/material/Tooltip';
import { useState } from "react";
import Stack from '@mui/material/Stack';
// import Button from '@mui/material/Button';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import React from "react";



const useStyles = makeStyles((theme) => ({
    fab: {
        position: 'fixed',
        bottom: '20px',
        right: '20px',
    },
    container: {
        width: '500px',
        height: '550px',
        backgroundColor: 'white',
        position: 'absolute',
        top: 0,
        bottom: 0,
        left: 0,
        right: 0,
        margin: 'auto',
        [theme.breakpoints.down('sm')]: {
            width: '100vh',
            height: '100vh',
            paddingRight:"500px !importent"
        },
    },
    form: {
        padding: theme.spacing(1),
        paddingRight:'150px'

    },
    item: {
        padding: theme.spacing(0),

    },
    inneritem: {
        paddingBottom: theme.spacing(2),
        paddingLeft: theme.spacing(0)

    },



}));

const Add = () => {

    const [open, setOpen] = useState(false);
    const [openAlert, setOpenAlert] = useState(false)

    const Alert = React.forwardRef(function Alert(props, ref) {
        return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
      });
      const handleClick = () => {
        setOpenAlert(true);
      };
      const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setOpenAlert(false);
      };


    const classes = useStyles();
    return (
        <>
            <Tooltip title="Add" placement="top" onClick={() => setOpen(true)}>
                <Fab color='primary' className={classes.fab}>
                    <AddIcon />
                </Fab>
            </Tooltip>
            <Modal open={open} >
                <Container className={classes.container}>
                    <form action="" autoComplete='off' className={classes.form}>
                        <div className={classes.item}>
                            <TextField
                                id="filled-search"
                                label="Title"
                                type="text"
                                className={classes.inneritem}
                                size='small'
                                style={{ width: '100%',margin:'auto'  ,marginBottom:'30px'}}

                            />
                            <TextField
                                id="filled-search"
                                label="
                                Discreption"
                                type="text"
                                size='small'
                                className={classes.inneritem}
                                rows={4}
                                style={{ width: '100%' ,marginBottom:'25px'}}
                                variant='outlined'
                                defaultValue='Tell about your experiance'

                            />
                            <TextField
                                id="outlined-select-currency"
                                select
                                label="visibility"
                                className={classes.inneritem}
                                value='Public'
                                helperText="Please select your currency"
                            >
                                <MenuItem value='Public'>Public</MenuItem>
                                <MenuItem value='Pricated'>Private</MenuItem>
                                <MenuItem value='Private'>Costum</MenuItem>
                                <MenuItem value='Public'>Disallowed</MenuItem>
                            </TextField>
                        </div>
                        <div className="">
                            <FormLabel component="legend">We can Comment?</FormLabel>
                            <RadioGroup row aria-label="gender" style={{ display: 'flex', flexDirection: 'column' }} name="row-radio-buttons-group">
                                <FormControlLabel value="Everybody" control={<Radio size='small' />} label="Everybody" />
                                <FormControlLabel value="Nobody" control={<Radio size='small' />} label="Nobody" />
                                <FormControlLabel value="Costom" control={<Radio size='small' />} label="Costom" />
                            </RadioGroup>
                        </div>
                        <div className={classes.item}style={{paddingTop:'30px'}}>
                            <Button variant='outlined' color='primary'
                            onClick={() =>{ setOpenAlert(true)
                            setTimeout(() => {
                                setOpen(false)
                            }, 200);
                            
                            }} style={{ marginRight: '30px' }}>Create</Button>
                            <Button variant='outlined' color='secondary' onClick={() => setOpen(false)}>Cancel</Button>

                        </div>

                    </form>
                </Container>
            </Modal>
            <Snackbar open={openAlert} autoHideDuration={5000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
                    This is a success message!
                </Alert>
            </Snackbar>

        </>
    )
}

export default Add
