import { alpha, AppBar, Avatar, InputBase, makeStyles, Toolbar, Typography } from "@material-ui/core"
import { Cancel, Notifications, Search } from "@material-ui/icons";
import Badge from '@mui/material/Badge';
import MailIcon from '@mui/icons-material/Mail';
import shams1 from '../imgs/s-img-1.png'
import { useState } from "react";

const useStyles = makeStyles((theme) => ({
    toolbar: {
        display: 'flex',
        justifyContent: 'space-between',
        // alignItems: 'center'
    },
    logoLg: {
        display: 'none',
        [theme.breakpoints.up("sm")]: {
            display: 'block'
        }
    },
    logoSM: {
        display: 'block',
        [theme.breakpoints.up("sm")]: {
            display: 'none'
        }
    },
    search: {
        display: 'flex',
        alignItems: 'center',
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: alpha(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: alpha(theme.palette.common.white, 0.25),
            marginLeft: 0,
            width: 'auto',
            [theme.breakpoints.up("sm")]: {
                marginLeft: theme.spacing(1),
                
            },
        },
        [theme.breakpoints.down("sm")]:{
            display: (props)=>  (props.open ? 'flex':'none'),
            width:'auto'
        }

    },

    input: {
        color: 'white',
        marginLeft: theme.spacing(1),
    },
    icons: {
        display: (props)=>  (props.open ? 'none':'flex'),
        alignItems:'center'
    },
    badge:{
        marginRight:theme.spacing(2)
    },

    searchbtn:{
        marginRight: theme.spacing(2),
        [theme.breakpoints.up("sm")]:{
            display:'none'
        },
        [theme.breakpoints.down("sm")]:{
            display: (props)=>  (props.open ? 'none':'flex'),
        }
    },
    cancel:{
        [theme.breakpoints.up("md")]:{
            display: 'none'
        }
    }




}))
const Navbar = () => {
const [open, setOpen] = useState(false);

    const classes = useStyles({open});
    return (
        <>
            <AppBar position='sticky'>
                <Toolbar className={classes.toolbar}>
                    <Typography variant="h6" className={classes.logoLg}>
                        Lame Dev
                    </Typography>
                    <Typography variant="h6" className={classes.logoSM}>
                        LAMA
                    </Typography>
                    <div className={classes.search}>
                        <Search />
                        <InputBase placeholder='Search...' className={classes.input} />
                        <Cancel className={classes.cancel} color='secondary' onClick={()=> setOpen(false)} />
                    </div>

                    <div className={classes.icons}>
                        <Search className={classes.searchbtn} onClick={()=>setOpen(true)} />
                        <Badge badgeContent={4} className={classes.badge} color="error">
                            <MailIcon color="action" />
                        </Badge>
                        <Badge badgeContent={4} className={classes.badge} color="error">
                            <Notifications color="action" />
                        </Badge>
                        <Avatar alt="shams" src={shams1} />
                    </div>
                </Toolbar>
            </AppBar>
        </>
    )
}

export default Navbar
